<template>
  <div class="c-form-postcode">
    <form @submit.prevent="onSubmit">
      <div class="c-form-filter__body">
        <h4>Modes of Delivery</h4>
        <CheckboxGroup v-model="form.deliveries" :items="deliveries"> </CheckboxGroup>
      </div>
      <div class="c-form-filter__footer">
        <div class="c-form-filter__buttons">
          <button class="btn btn-secondary btn-lg">Update Results</button>
          <button class="btn btn-lg" @click="onBack">Cancel and Go Back</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Dropdown from '@/components/global/dropdown';
import Accordion from '@/components/global/accordion';
import CheckboxGroup from '@/components/map/checkbox-group';

import {
  statuses,
  stages,
  deliveries,
  professions,
  costs,
  services,
  languages
} from '@/lib/form-data.js';

import slugify from '@/lib/slugify.js';

import VueSlider from 'vue-slider-component';

export default {
  components: {
    Dropdown,
    Accordion,
    CheckboxGroup,
    VueSlider
  },

  data: function() {
    return {
      statuses: statuses,
      stages: stages,
      deliveries: deliveries,
      professions: professions,
      costs: costs,
      services: services,
      languages: languages,

      // gets populated by the watcher
      form: {}
    };
  },

  watch: {
    filter: {
      handler: function() {
        this.form = this.setupForm(this.filter);
      },
      immediate: true
    }
  },

  computed: {
    debug: function() {
      return this.$store.getters['debug'];
    },
    filter: function() {
      return this.$store.getters['map/filter'];
    }
  },

  methods: {
    onStatusClick: function(value) {
      this.form.help_for = value;
      this.$refs.dropdownHelpFor.onToggle();
    },
    setupForm: function(filter) {
      let form = JSON.parse(JSON.stringify(filter));

      return form;
    },
    slugify: function(str) {
      return slugify(str);
    },
    async onSubmit() {
      let form = this.form;
      await this.$store.dispatch('map/updateFilter', form);

      this.$emit('submit');
    },
    onBack: function() {
      this.$emit('back');
    }
  }
};
</script>

