<template>
  <div class="c-form-sub-profession">
    <form @submit.prevent="onSubmit">
      <div class="c-form-sub-profession__body">
        <h4>Seeking Help for</h4>
        <Dropdown ref="dropdownHelpFor">
          <template v-slot:input>
            <span>{{ professionTitle }}</span>
          </template>
          <template>
            <Checkbox
              ref="professionCheckboxGroup"
              v-if="form.help_for"
              v-model="form.expertise"
              :items="stages[form.help_for]"
              :topLevelToggle="form.help_for !== 'grief_and_loss'"
            ></Checkbox>
          </template>
        </Dropdown>
      </div>
      <FormFooter @onBack="onBack"></FormFooter>
    </form>
  </div>
</template>

<script>
import Dropdown from '@/components/global/dropdown';
import Accordion from '@/components/global/accordion';
import FormFooter from '@/components/global/form-footer';

import Checkbox from '@/components/checkbox';

import {
  statuses,
  stages,
  deliveries,
  professions,
  costs,
  services,
  languages
} from '@/lib/form-data.js';

import slugify from '@/lib/slugify.js';

import VueSlider from 'vue-slider-component';

export default {
  components: {
    Dropdown,
    Accordion,
    VueSlider,
    FormFooter,
    Checkbox
  },

  data: function() {
    return {
      statuses: statuses,
      stages: stages,
      deliveries: deliveries,
      professions: professions,
      costs: costs,
      services: services,
      languages: languages,

      // gets populated by the watcher
      form: {}
    };
  },

  watch: {
    filter: {
      handler: function() {
        this.form = this.setupForm(this.filter);
      },
      immediate: true
    }
  },

  computed: {
    professionTitle: function() {
      let str = '0 out of 0 topics selected';

      let help_for = this.form.help_for;

      if (!help_for) {
        return str;
      }

      let count = 0;

      let stage = this.stages[help_for];

      stage.map(item => {
        if (item.items) {
          item.items.map(subItem => {
            count += 1;
          });
        } else {
          count += 1;
        }
      });

      let currentCount = this.form.expertise.length;

      str = `${currentCount} out of ${count} topics selected`;

      return str;
    },
    debug: function() {
      return this.$store.getters['debug'];
    },
    filter: function() {
      return this.$store.getters['map/filter'];
    }
  },

  methods: {
    onStatusClick: function(value) {
      this.form.help_for = value;
      this.$refs.dropdownHelpFor.onToggle();

      // We do this cuz the perinatal ref won't be available without it
      this.$nextTick(() => {
        if (this.$refs.professionCheckboxGroup) {
          this.$refs.professionCheckboxGroup.checkAll();
        } else {
          console.log('ref not accessible');
          this.$nextTick(() => {
            if (this.$refs.professionCheckboxGroup) {
              this.$refs.professionCheckboxGroup.checkAll();
            } else {
              console.log('ref still not accessible');
            }
          });
        }
      });
    },
    setupForm: function(filter) {
      let form = JSON.parse(JSON.stringify(filter));

      return form;
    },
    slugify: function(str) {
      return slugify(str);
    },
    async onSubmit() {
      let form = this.form;
      await this.$store.dispatch('map/updateFilter', form);

      this.$emit('submit');
    },
    onBack: function() {
      this.$emit('back');
    }
  },

  mounted: function() {
    if (this.form.help_for) {
      this.$nextTick(() => {
        this.$refs.professionCheckboxGroup.checkAll();
      });
    }
  }
};
</script>

