  &__body {
    padding: var(--margin-lg) var(--spacer);

    &__inner {
      display: flex;
      align-items: center;

      > p {
        margin-bottom: 0;
      }
    }
  }

  &__buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: var(--spacer-sm) var(--spacer);
  }

  &__disclaimer {
    padding: var(--margin-lg) var(--spacer);
