/**
 *
 * Turns an object to a human readable string
 * Used to turn filter into a readable label
 */
function deconstructObject(obj) {
  let str = ``;
  let val = '';
  let count = 0;
  for (const prop in obj) {
    if (obj.hasOwnProperty(prop)) {
      if (typeof obj[prop] === 'string' || typeof obj[prop] === 'number') {
        val = obj[prop] + '';
      }

      if (typeof obj[prop] === 'array') {
        count = obj[prop].length;
        val = obj[prop].join(',');
      }

      if (!val) {
        continue;
      }

      str = str + `${prop}:${val}`;

      if (count > 0) {
        str = str + `[${count}]`;
      }

      str = str + ` | `;

      count = 0;
      val = ';';
    }
  }

  return str;
}

let store = {
  namespaced: true,
  state: {
    analytics: null
  },

  getters: {},

  mutations: {},

  actions: {
    async init(store) {
      await store.dispatch('setAnalytics');
    },

    async setAnalytics(store) {
      let debug = store.rootGetters['debug'];

      if (debug) {
        return;
      }

      if (window.gtag) {
        store.state.analytics = await store.dispatch('setupGtag');
      } else {
      }
    },

    async setupGtag(store) {
      if (!window.gtag) {
        return;
      }

      let gtag = window.gtag;
      gtag('js', new Date());

      gtag('config', 'UA-148357023-3', {
        custom_map: { dimension1: 'email' }
      });

      return gtag;
    },

    async send(store, event) {
      let debug = store.rootGetters['debug'];

      if (debug) {
        return;
      }

      if (!store.state.analytics) {
        store.state.analytics = await store.dispatch('setupGtag');
      }

      if (!store.state.analytics) {
        //send data to server and to ga's servers via measurement api?
        //for now, we aren't doing anything
        return false;
      }

      let payload = {
        event_category: event.category,
        event_callback: function(e) {}
      };

      if (event.label) {
        payload['event_label'] = event.label;
      }

      if (event.value) {
        payload['value'] = event.value;
      }

      if (event.email) {
        payload['email'] = event.email;
      }

      store.state.analytics('event', event.action, payload);
    },

    /**
     * Send user based information about what data point was interacted, eg: did someone click on a user's phone number, email, etc.
     */
    onUserInteraction: function(store, payload) {
      let user = payload.user;
      let event = payload.event;
      let action = `click`;
      let category = `interaction`;
      let value = 1;
      let label = `${event} link interaction`;

      let email = user.email;

      let data = {
        action,
        category,
        label,
        value,
        email
      };

      store.dispatch('send', data);
    },

    /**
     * Send filters data
     */
    onSearchFilter: function(store, filter) {
      let action = 'search';
      let category = 'filter';
      let label = deconstructObject(filter);

      let data = {
        action,
        category,
        label
      };

      store.dispatch('send', data);
    },

    /**
     * Send search result data like how many listings are shown, how many are telehealth, etc
     */
    onAfterSearch: function(store) {
      let action = 'search:filter';
      let category = 'search:after';
      let value = store.rootGetters['users/filteredUsers'].length;

      let data = {
        action,
        category,
        value
      };

      store.dispatch('send', data);
    },

    onSaveSearch: function(store, form) {
      let action = 'form:submit';
      let category = 'save-search';
      let label = `Name: ${form.name} | Email : ${form.email}`;

      let data = {
        action,
        category,
        label
      };

      store.dispatch('send', data);
    }
  },
  modules: {}
};

export default store;
