<template>
  <div class="c-form-radius">
    <form @submit.prevent="onSubmit">
      <div class="c-form-radius__body">
        <h4>Search Radius</h4>
        <div class="c-form-radius__body__inner">
          <p>
            <input v-model="form.radius" class="form-control" type="number" min="1" max="50" />
            km
          </p>
          <vue-slider
            tooltip="none"
            :contained="true"
            :adsorb="true"
            :min="1"
            :max="50"
            v-model="form.radius"
            v-if="form.radius"
          />
        </div>
      </div>
      <FormFooter @onBack="onBack"></FormFooter>
    </form>
  </div>
</template>

<script>
import Dropdown from '@/components/global/dropdown';
import Accordion from '@/components/global/accordion';
import CheckboxGroup from '@/components/map/checkbox-group';
import FormFooter from '@/components/global/form-footer';

import {
  statuses,
  stages,
  deliveries,
  professions,
  costs,
  services,
  languages
} from '@/lib/form-data.js';

import slugify from '@/lib/slugify.js';

import VueSlider from 'vue-slider-component';

export default {
  components: {
    Dropdown,
    Accordion,
    CheckboxGroup,
    VueSlider,
    FormFooter
  },

  data: function() {
    return {
      statuses: statuses,
      stages: stages,
      deliveries: deliveries,
      professions: professions,
      costs: costs,
      services: services,
      languages: languages,

      // gets populated by the watcher
      form: {}
    };
  },

  watch: {
    filter: {
      handler: function() {
        this.form = this.setupForm(this.filter);
      },
      immediate: true
    }
  },

  computed: {
    debug: function() {
      return this.$store.getters['debug'];
    },
    filter: function() {
      return this.$store.getters['map/filter'];
    }
  },

  methods: {
    onStatusClick: function(value) {
      this.form.help_for = value;
      this.$refs.dropdownHelpFor.onToggle();
    },
    setupForm: function(filter) {
      let form = JSON.parse(JSON.stringify(filter));

      return form;
    },
    slugify: function(str) {
      return slugify(str);
    },
    async onSubmit() {
      let form = this.form;
      await this.$store.dispatch('map/updateFilter', form);

      this.$emit('submit');
    },
    onBack: function() {
      this.$emit('back');
    }
  }
};
</script>

